import { Mail, MoodBad, Notifications } from "@mui/icons-material";
import {
  AppBar,
  Avatar,
  Box,
  InputBase,
  Menu,
  MenuItem,
  Toolbar,
  Typography,
  styled,
} from "@mui/material";
import React, { useState } from "react";
import Badge from "@mui/material/Badge";

const StyledToolBar = styled(Toolbar)({
  display: "flex",
  justifyContent: "space-between",
});
const Search = styled("div")(({ theme }) => ({
  backgroundColor: theme.palette.secondary.light,
  width: "50%",
  padding: ".5rem .9rem",
  borderRadius: ".5rem",
}));
const Icons = styled(Box)(({ theme }) => ({
  display: "none",

  alignItems: "center",
  gap: "2rem",
  "&:hover": {
    cursor: "pointer",
  },
  [theme.breakpoints.up("sm")]: {
    display: "flex",
  },
}));
const UserBox = styled(Box)(({ theme }) => ({
  display: "flex",
  gap: ".5rem",
  alignItems: "center",
  [theme.breakpoints.up("sm")]: {
    display: "none",
  },
}));

const Navbar = () => {
  const [openMenu, setOpenMenu] = useState<boolean>(false);
  const handleOpenMenu = () => {
    setOpenMenu(!openMenu);
  };
  return (
    <AppBar position="fixed">
      <StyledToolBar>
        <Typography sx={{ display: { xs: "none", sm: "block" } }} variant="h5">
          mySocial
        </Typography>
        <MoodBad
          fontSize="large"
          sx={{ display: { xs: "block", sm: "none" } }}
        />
        <Search>
          <InputBase fullWidth placeholder="Search" />
        </Search>
        <Icons>
          <Badge badgeContent={2} color="error">
            <Mail fontSize="medium" />
          </Badge>
          <Badge badgeContent={5} color="error">
            <Notifications fontSize="medium" />
          </Badge>
          <Avatar
            onClick={(e) => setOpenMenu(true)}
            alt="Profile Picture"
            src="data:image/jpeg;base64,"
          />
        </Icons>
        <UserBox onClick={handleOpenMenu}>
          <Avatar
            alt="Profile Picture"
            src="data:image/jpeg;base64,"
          />
          <Typography variant="h6">Prajwal</Typography>
        </UserBox>
      </StyledToolBar>
      <Menu
        id="demo-positioned-menu"
        aria-labelledby="demo-positioned-button"
        open={openMenu}
        onClose={(e) => setOpenMenu(false)}
        anchorOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "right",
        }}
      >
        <MenuItem>Profile</MenuItem>
        <MenuItem>My account</MenuItem>
        <MenuItem>Logout</MenuItem>
      </Menu>
    </AppBar>
  );
};

export default Navbar;
